import { DataProvider, GetListParams, GetListResult } from 'react-admin'
import { ENTRANCES_URL, PALMS_URL } from '../api-urls'
import { get, getWithPagination } from '../common/fetch.utils'
import { ALL_RESULTS } from '../common/filter.values'
import { EntranceDto } from '../dto/entrance.dto'
import { PalmDto } from '../dto/device/palm/palm.dto'
import { Entrance } from './entrance'
import { mapSortEntranceParam } from './entrance-sort.mapper'

interface EntranceListParams extends GetListParams {
  readonly sort: {
    readonly field: keyof EntranceDto
    readonly order: 'ASC' | 'DESC'
  }
  readonly filter:
  {
    readonly note: string
    readonly palmId: string
  }
}

const provider = {
  getList: async (
    resource: string,
    {
      filter: { note, palmId },
      sort: { field, order },
      pagination: { page, perPage }
    }: EntranceListParams,
  ): Promise<GetListResult<Entrance>> => {
    const attachedEntrances: number[] = []
    if (palmId) {
      const palm = await get<PalmDto>(PALMS_URL, `/${palmId}`)
      attachedEntrances.push(...palm.entrancesIds)
    }

    const queryParams: string[] = []
    queryParams.push(`sortBy=${mapSortEntranceParam(field)}`)
    queryParams.push(`sortOrder=${order}`)
    queryParams.push(`rangeFrom=${page * perPage - perPage}`)
    queryParams.push(`rangeTo=${page * perPage}`)
    const pathParams = queryParams.reduce((p, c) => `${p}&${c}`)
    const { data, range: { total } } = await getWithPagination<EntranceDto[]>(`${ENTRANCES_URL}/GetByConditions/`, `${note ? `e=>e.Note.Contains("${note}")` : ALL_RESULTS}?${pathParams}`)

    const entrances = data.map((entrance) => ({ ...entrance, attached: attachedEntrances.includes(entrance.id) }))

    return Promise.resolve({
      data: entrances,
      total
    })
  }
} as DataProvider

export default provider