import { DataProvider, GetListResult, GetOneParams, GetOneResult, UpdateParams, UpdateResult, GetListParams, Identifier } from "react-admin"
import { EVENTS_URL, PALMS_URL } from "../api-urls"
import { get, patch, put, getWithPagination } from "../common/fetch.utils"
import { ALL_RESULTS } from "../common/filter.values"
import { EventDto } from "../dto/event/event.dto"
import { Event } from "./event"
import { DateTime } from "../common/date-time"
import { EventCompaniesDto } from "../dto/event/event-companies.dto"
import { EventDevicesDto } from "../dto/event/event-devices.dto"
import { EventObjectsDto } from "../dto/event/event-objects.dto"
import { EventStatus } from "../enum/EventStatus"
import { mapSortEventParam } from './event-sort.mapper'
import { CacheableDataProviderExtensionResult, DataProviderExtensionResult } from "../common/data-provider"
import { PalmDto } from "../dto/device/palm/palm.dto"

type EventUpdateRequest = {
  id: number,
  name?: string,
  dateStart?: DateTime,
  dateEnd?: DateTime,
  status?: EventStatus
}

interface EventListParams extends GetListParams {
  readonly sort: {
    readonly field: keyof EventDto
    readonly order: 'ASC' | 'DESC'
  }
  readonly filter:
  {
    readonly name: string
    readonly palmId: string
  }
}

const provider = {
  getList: async (
    resource: string,
    {
      filter: { name, palmId },
      sort: { field, order },
      pagination: { page, perPage }
    }: EventListParams,
  ): Promise<GetListResult<Event>> => {
    const attachedEvents: number[] = []
    if (palmId) {
      const palm = await get<PalmDto>(PALMS_URL, `/${palmId}`)
      attachedEvents.push(...palm.eventsIds)
    }

    const queryParams: string[] = []
    queryParams.push(`sortBy=${mapSortEventParam(field)}`)
    queryParams.push(`sortOrder=${order}`)
    queryParams.push(`rangeFrom=${page * perPage - perPage}`)
    queryParams.push(`rangeTo=${page * perPage}`)
    const pathParams = queryParams.reduce((p, c) => `${p}&${c}`)
    const { data, range: { total } } = await getWithPagination<EventDto[]>(`${EVENTS_URL}/GetByConditions/`, `${name ? `e=>e.Name.Contains("${name}")` : ALL_RESULTS}?${pathParams}`)

    const events = data.map((event) => ({ ...event, attached: attachedEvents.includes(event.id) }))

    return Promise.resolve({
      data: events,
      total
    })
  },
  getOne: async (
    resource: string,
    { id }: GetOneParams
  ): Promise<GetOneResult<EventDto>> => {
    const event = await get<EventDto>(EVENTS_URL, `/${id}`)

    return {
      data: event
    }
  },
  update: async (
    resource: string,
    { id, data, previousData }: UpdateParams
  ): Promise<UpdateResult> => {
    const { name, dateStart, dateEnd } = data

    let eventUpdate = data
    if (previousData) {
      const { name: previousName, dateStart: previousDateStart, dateEnd: previousDateEnd } = previousData
      eventUpdate = await patch<EventUpdateRequest, EventDto>(EVENTS_URL, {
        id: parseInt(id as string, 10),
        name: name ?? previousName,
        dateStart: dateStart ?
          new Date(new Date(dateStart).getTime() - (new Date(dateStart).getTimezoneOffset() * 60000)).toJSON() as DateTime :
          previousDateStart,
        dateEnd: dateEnd ?
          new Date(new Date(dateEnd).getTime() - (new Date(dateEnd).getTimezoneOffset() * 60000)).toJSON() as DateTime :
          previousDateEnd
      })
    }

    return Promise.resolve({
      data: { id, ...data, ...eventUpdate }
    })
  },
  activate: async (
    resource: string,
    { eventId }: ChangeStateParams
  ): Promise<DataProviderExtensionResult<ChangeStateResponse>> => {
    const activate = await put<undefined, ChangeStateResponse>
      (`${EVENTS_URL}/ActivateAsyncJob/${eventId}`, undefined)
    return {
      data: activate
    }
  },
  deactivate: async (
    resource: string,
    { eventId }: ChangeStateParams
  ): Promise<DataProviderExtensionResult<ChangeStateResponse>> => {
    const deactivate = await put<undefined, ChangeStateResponse>
      (`${EVENTS_URL}/DeactivateAsyncJob/${eventId}`, undefined)
    return {
      data: deactivate
    }
  },
  getCompaniesList: async (
    resource: string,
    { eventId }: GetCompaniesParams
  ): Promise<CacheableDataProviderExtensionResult<EventCompaniesDto>> => {
    const companies = await put<undefined, EventCompaniesDto>
      (`${EVENTS_URL}/GetCompanies/${eventId}`, undefined)
    return {
      data: companies
    }
  },
  attachCompanies: async (
    resource: string,
    params: EventCompaniesDto
  ): Promise<CacheableDataProviderExtensionResult<EventCompaniesDto>> => {
    const eventCompanies = await put<EventCompaniesDto, EventCompaniesDto>
      (`${EVENTS_URL}/AttachCompanies`, params)
    return {
      data: eventCompanies
    }
  },
  detachCompanies: async (
    resource: string,
    params: EventCompaniesDto
  ): Promise<CacheableDataProviderExtensionResult<EventCompaniesDto>> => {
    const eventCompanies = await put<EventCompaniesDto, EventCompaniesDto>
      (`${EVENTS_URL}/DetachCompanies`, params)
    return {
      data: eventCompanies
    }
  },
  attachObjects: async (
    resource: string,
    params: EventObjectsDto
  ): Promise<CacheableDataProviderExtensionResult<EventObjectsDto>> => {
    const eventObjects = await put<EventObjectsDto, EventObjectsDto>
      (`${EVENTS_URL}/AttachObjects`, params)
    return {
      data: eventObjects
    }
  },
  detachObjects: async (
    resource: string,
    params: EventObjectsDto
  ): Promise<CacheableDataProviderExtensionResult<EventObjectsDto>> => {
    const eventObjects = await put<EventObjectsDto, EventObjectsDto>
      (`${EVENTS_URL}/DetachObjects`, params)
    return {
      data: eventObjects
    }
  },
  getDevicesList: async (
    resource: string,
    { eventId }: GetDevicesParams
  ): Promise<CacheableDataProviderExtensionResult<EventDevicesDto>> => {
    const devices = await put<undefined, EventDevicesDto>
      (`${EVENTS_URL}/GetDevices/${eventId}`, undefined)
    return {
      data: devices
    }
  },
  attachDevices: async (
    resource: string,
    params: EventDevicesDto
  ): Promise<CacheableDataProviderExtensionResult<EventDevicesDto>> => {
    const eventDevices = await put<EventDevicesDto, EventDevicesDto>
      (`${EVENTS_URL}/AttachDevices`, params)
    return {
      data: eventDevices
    }
  },
  detachDevices: async (
    resource: string,
    params: EventDevicesDto
  ): Promise<CacheableDataProviderExtensionResult<EventDevicesDto>> => {
    const eventDevices = await put<EventDevicesDto, EventDevicesDto>
      (`${EVENTS_URL}/DetachDevices`, params)
    return {
      data: eventDevices
    }
  }
} as EventDataProvider

interface EventDataProvider extends DataProvider {
  activate: (
    resource: string,
    params: ChangeStateParams
  ) => Promise<DataProviderExtensionResult<ChangeStateResponse>>;

  deactivate: (
    resource: string,
    params: ChangeStateParams
  ) => Promise<DataProviderExtensionResult<ChangeStateResponse>>;

  getCompaniesList: (
    resource: string,
    params: GetCompaniesParams
  ) => Promise<CacheableDataProviderExtensionResult<EventCompaniesDto>>;

  attachCompanies: (
    resource: string,
    params: EventCompaniesDto
  ) => Promise<CacheableDataProviderExtensionResult<EventCompaniesDto>>;

  detachCompanies: (
    resource: string,
    params: EventCompaniesDto
  ) => Promise<CacheableDataProviderExtensionResult<EventCompaniesDto>>;

  getObjectsList: (
    resource: string,
    params: GetObjectsParams
  ) => Promise<CacheableDataProviderExtensionResult<EventObjectsDto>>;

  attachObjects: (
    resource: string,
    params: EventObjectsDto
  ) => Promise<CacheableDataProviderExtensionResult<EventObjectsDto>>;

  detachObjects: (
    resource: string,
    params: EventObjectsDto
  ) => Promise<CacheableDataProviderExtensionResult<EventObjectsDto>>;

  getDevicesList: (
    resource: string,
    params: GetDevicesParams
  ) => Promise<CacheableDataProviderExtensionResult<EventDevicesDto>>;

  attachDevices: (
    resource: string,
    params: EventDevicesDto
  ) => Promise<CacheableDataProviderExtensionResult<EventDevicesDto>>;

  detachDevices: (
    resource: string,
    params: EventDevicesDto
  ) => Promise<CacheableDataProviderExtensionResult<EventDevicesDto>>;
};

export interface ChangeStateParams {
  eventId: Identifier;
}

export interface ChangeStateResponse {
  jobId: Identifier;
}

export interface GetCompaniesParams {
  eventId: Identifier;
}

export interface GetObjectsParams {
  eventId: Identifier;
}

export interface GetDevicesParams {
  eventId: Identifier;
}

export default provider