import { Box, Divider, Typography } from '@material-ui/core'
import * as React from 'react'
import {
  Datagrid,
  List,
  ListProps,
  TextField,
  useListContext,
  useTranslate,
  FunctionField,
  Record,
  Pagination,
  TextInput
} from 'react-admin'
import { ResourceName } from '../../core/ResourceName'
import { MaskField } from '../common/MaskField'

const TicketInfoGrid = () => {
  const {
    loading,
    filterValues: { ticketCode },
    data
  } = useListContext()
  const t = useTranslate()
  return (
    <>
      {ticketCode &&
        <Box m={2} display="inline-flex" height="40px">
          <Typography variant="h5" paragraph>
            {t('resources.ticket-info.search-list-title', { ticketCode })}
          </Typography>
        </Box>
      }
      <Divider />
      <Datagrid
        loaded={!loading}
        rowClick={(id) => `${ResourceName.TICKET_DETAILS}/${data[id]?.bookingId}/show`}
      >
        <TextField source="firstname" sortable={false} />
        <TextField source="lastname" sortable={false} />
        <MaskField source="pesel" sortable={false} />
        <TextField source="ticketCode" sortable />
        <FunctionField
          label='resources.ticket-info.fields.eventName'
          render={(record: Record | undefined) => `${record?.eventName} ${record?.eventDate.replace("T", " ")} ${record?.eventId}`} />
        <TextField source="bookingId" sortable />
      </Datagrid>
    </>
  )
}

export const TicketInfoList = (props: ListProps) => (
  <List exporter={false} perPage={20} sort={{ field: "id", order: "DESC" }}
    pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />} {...props}
    filters={[
      <TextInput source="ticketCode" alwaysOn />
    ]}>
    <TicketInfoGrid />
  </List>
)
